import _ from 'lodash';
import { getCharacterImages, resetDualSoulSession } from './character-assets';
import { Schema } from './schema';
import { getDefaultTheme, getThemeById, themes } from './themes';
/**
 * 从消息内容中解析 <visual_cards> 标签
 * @param content 消息内容
 * @returns 解析出的视觉卡片数组
 */
function parseVisualCards(content) {
    try {
        // 匹配 <visual_cards>...</visual_cards> 标签
        const match = content.match(/<visual_cards>\s*([\s\S]*?)\s*<\/visual_cards>/);
        if (!match) {
            return [];
        }
        const jsonStr = match[1].trim();
        if (!jsonStr) {
            return [];
        }
        // 解析 JSON 数组
        const cards = JSON.parse(jsonStr);
        // 验证数据结构
        if (!Array.isArray(cards)) {
            console.warn('[图鉴] visual_cards 不是数组');
            return [];
        }
        return cards.filter(card => card.name &&
            card.img_code &&
            typeof card.back_text === 'string');
    }
    catch (e) {
        console.error('[图鉴] 解析 visual_cards 失败', e);
        return [];
    }
}
/**
 * 将 AI 输出的视觉卡片转换为图鉴卡片
 * @param visualCards AI 输出的视觉卡片
 * @returns 图鉴卡片数组
 */
function convertToGalleryCards(visualCards) {
    // 重置虞汐颜会话缓存
    resetDualSoulSession();
    const result = [];
    for (const card of visualCards) {
        // 使用新的 getCharacterImages 函数获取正面和背面图片
        const images = getCharacterImages(card.name, card.img_code, card.back_img_code);
        // 如果返回 null（虞汐颜重复），则跳过
        if (images === null) {
            console.info(`[图鉴] 跳过重复的 ${card.name} 卡片`);
            continue;
        }
        result.push({
            name: card.name,
            front: images.front,
            back: images.back,
            backText: card.back_text || '',
            isFlipped: false,
            frontName: images.frontName,
            backName: images.backName,
        });
    }
    return result;
}
export const useDataStore = defineStore('data', errorCatched(() => {
    const message_id = getCurrentMessageId();
    // 从 MVU 变量中获取 stat_data
    const rawData = ref(null);
    // 图鉴卡片数据
    const galleryCards = ref([]);
    // 卡片缓存 - 避免重复解析
    const cardCache = new Map();
    // 初始化数据（带重试机制）
    const initData = (retryCount = 0) => {
        try {
            const variables = getVariables({ type: 'message', message_id });
            const statData = _.get(variables, 'stat_data', null);
            if (statData && Object.keys(statData).length > 0) {
                // 使用 Schema.parse 来解析和验证数据
                // 🔧 修复：强制深拷贝，确保 Vue 能检测到嵌套对象变化
                rawData.value = _.cloneDeep(Schema.parse(statData));
            }
            else if (statData === null || statData === undefined) {
                // stat_data 为 null 或 undefined，使用空对象初始化以触发所有默认值
                const defaultData = Schema.parse({});
                rawData.value = defaultData;
                console.info('[踏月寻仙] 使用默认值初始化变量数据', {
                    等级: defaultData.本尊.等级,
                    灵根: defaultData.本尊.灵根,
                    体质: defaultData.本尊.体质,
                    功法: defaultData.本尊.功法,
                });
                toastr.info('当前楼层无数据，已使用默认值初始化', '踏月寻仙');
                // 解析当前楼层消息中的 visual_cards
                parseCurrentMessageCards();
                return; // 直接返回，不再重试
            }
            console.info('[踏月寻仙] 成功加载变量数据', {
                等级: rawData.value?.本尊.等级,
                灵根: rawData.value?.本尊.灵根,
                体质: rawData.value?.本尊.体质,
                功法: rawData.value?.本尊.功法,
            });
            // 解析当前楼层消息中的 visual_cards
            parseCurrentMessageCards();
        }
        catch (e) {
            // Schema.parse 失败或其他错误
            console.error('[踏月寻仙] 初始化数据失败', e);
            if (retryCount < 5) {
                // 数据还没准备好，重试（减少重试次数到5次，固定延迟200ms，总计最多等待1秒）
                console.info(`[踏月寻仙] 数据未就绪，200ms后第${retryCount + 1}次重试`);
                setTimeout(() => {
                    initData(retryCount + 1);
                }, 200); // 固定延迟200ms
            }
            else {
                console.warn('[踏月寻仙] 重试次数已用完，尝试手动触发MVU解析');
                // 尝试手动触发MVU解析当前消息
                tryManualParseMvuMessage();
            }
        }
    };
    // 手动触发MVU解析当前消息（用于初始楼层）
    const tryManualParseMvuMessage = async () => {
        try {
            // 检查MVU是否已初始化
            await waitGlobalInitialized('Mvu');
            // 获取当前消息内容
            const messages = getChatMessages(message_id);
            if (messages.length === 0) {
                console.warn('[踏月寻仙] 无法获取当前消息内容');
                fallbackToInheritOrDefault();
                return;
            }
            const content = messages[0].message || '';
            console.info('[踏月寻仙] 尝试手动触发MVU解析，消息长度:', content.length);
            // 获取当前的MVU数据（可能为空）
            const currentMvuData = Mvu.getMvuData({ type: 'message', message_id });
            // 手动解析消息中的MVU指令
            const newMvuData = await Mvu.parseMessage(content, currentMvuData);
            // 将解析结果写回当前楼层
            await Mvu.replaceMvuData(newMvuData, { type: 'message', message_id });
            console.info('[踏月寻仙] MVU手动解析完成，重新加载数据');
            // 延迟后重新加载数据
            setTimeout(() => {
                const variables = getVariables({ type: 'message', message_id });
                const statData = _.get(variables, 'stat_data', null);
                if (statData && Object.keys(statData).length > 0) {
                    // 🔧 修复：强制深拷贝
                    rawData.value = _.cloneDeep(Schema.parse(statData));
                    console.info('[踏月寻仙] ✅ 手动解析后成功加载数据');
                    toastr.success('已成功初始化状态数据', '踏月寻仙');
                    parseCurrentMessageCards();
                }
                else {
                    console.warn('[踏月寻仙] 手动解析后仍无数据，使用备用方案');
                    fallbackToInheritOrDefault();
                }
            }, 500);
        }
        catch (e) {
            console.error('[踏月寻仙] 手动触发MVU解析失败', e);
            fallbackToInheritOrDefault();
        }
    };
    // 备用方案：继承或使用默认值（仅用于显示，不保存）
    const fallbackToInheritOrDefault = () => {
        console.warn('[踏月寻仙] 启用备用方案');
        // 尝试从上一楼层继承数据用于显示
        const inheritedData = tryInheritFromPreviousFloor();
        if (inheritedData) {
            // 成功继承数据，仅用于显示，不保存到当前楼层
            rawData.value = inheritedData;
            console.info('[踏月寻仙] 从上一楼层继承数据用于显示（不保存）');
            toastr.info('正在显示上一楼层的状态数据', '踏月寻仙');
        }
        else {
            // 无法继承，使用默认值
            console.warn('[踏月寻仙] 无法继承数据，使用默认值');
            rawData.value = Schema.parse({});
            toastr.info('使用默认状态数据', '踏月寻仙');
        }
        // 解析当前楼层消息中的 visual_cards
        parseCurrentMessageCards();
    };
    // 尝试从上一楼层继承数据
    const tryInheritFromPreviousFloor = () => {
        try {
            // 从当前楼层往前查找最近的有效数据
            // 获取所有聊天消息（从第0楼到最后一楼）
            const chatMessages = getChatMessages('0-{{lastMessageId}}');
            const currentIndex = chatMessages.findIndex(msg => msg.message_id === message_id);
            if (currentIndex === -1) {
                console.warn('[踏月寻仙] 无法找到当前楼层索引');
                return null;
            }
            // 从当前楼层往前遍历，查找最近的有效 stat_data
            for (let i = currentIndex - 1; i >= 0; i--) {
                const prevMessageId = chatMessages[i].message_id;
                const prevVariables = getVariables({ type: 'message', message_id: prevMessageId });
                const prevStatData = _.get(prevVariables, 'stat_data', null);
                if (prevStatData) {
                    // 找到有效数据，解析并返回
                    // 🔧 修复：强制深拷贝
                    const parsedData = _.cloneDeep(Schema.parse(prevStatData));
                    console.info(`[踏月寻仙] 从第 ${prevMessageId} 楼继承数据`);
                    return parsedData;
                }
            }
            console.warn('[踏月寻仙] 前面的楼层都没有有效数据');
            return null;
        }
        catch (e) {
            console.error('[踏月寻仙] 继承数据时出错', e);
            return null;
        }
    };
    // 解析当前楼层消息中的 visual_cards
    const parseCurrentMessageCards = () => {
        try {
            // 获取当前消息内容
            const messages = getChatMessages(message_id);
            if (messages.length > 0) {
                const content = messages[0].message || '';
                // 检查缓存
                const cacheKey = `${message_id}_${content.length}`;
                if (cardCache.has(cacheKey)) {
                    galleryCards.value = cardCache.get(cacheKey);
                    console.info('[图鉴] 使用缓存的角色卡片');
                    return;
                }
                const visualCards = parseVisualCards(content);
                if (visualCards.length > 0) {
                    const cards = convertToGalleryCards(visualCards);
                    galleryCards.value = cards;
                    // 缓存结果
                    cardCache.set(cacheKey, cards);
                    // 预加载图片
                    preloadImages(cards);
                    console.info('[图鉴] 解析到角色卡片', cards.length, '张');
                }
                else {
                    galleryCards.value = [];
                }
            }
        }
        catch (e) {
            console.error('[图鉴] 解析消息卡片失败', e);
            toastr.warning(`解析角色卡片失败: ${e instanceof Error ? e.message : '未知错误'}`, '图鉴');
        }
    };
    // 预加载图片
    const preloadImages = (cards) => {
        cards.forEach(card => {
            // 预加载正面图片
            const frontImg = new Image();
            frontImg.src = card.front;
            // 预加载背面图片
            const backImg = new Image();
            backImg.src = card.back;
        });
        console.info('[图鉴] 开始预加载', cards.length * 2, '张图片');
    };
    // 翻转卡片
    const toggleCardFlip = (index) => {
        if (galleryCards.value[index]) {
            galleryCards.value[index].isFlipped = !galleryCards.value[index].isFlipped;
        }
    };
    // 等待MVU框架初始化后再加载数据
    waitGlobalInitialized('Mvu').then(() => {
        console.info('[踏月寻仙] MVU 框架已初始化，开始加载数据');
        // 立即开始初始化，不再额外延迟
        initData();
    }).catch(() => {
        console.warn('[踏月寻仙] MVU 框架未加载，直接初始化');
        initData();
    });
    // 防抖定时器 - 避免短时间内重复加载
    let updateDebounceTimer = null;
    const debouncedInitData = (delay = 200) => {
        if (updateDebounceTimer) {
            clearTimeout(updateDebounceTimer);
        }
        updateDebounceTimer = setTimeout(() => {
            initData();
            updateDebounceTimer = null;
        }, delay);
    };
    // 监听消息更新事件，实时刷新数据
    eventOn(tavern_events.MESSAGE_UPDATED, (id) => {
        if (id === message_id) {
            console.info('[踏月寻仙] 检测到消息更新，防抖延迟200ms后重新加载');
            debouncedInitData(200);
        }
    });
    // 监听消息删除事件（重新roll时会触发）
    eventOn(tavern_events.MESSAGE_DELETED, (id) => {
        console.info('[踏月寻仙] 检测到消息删除（可能是重新roll），清空数据');
        if (id === message_id) {
            // 清空当前数据，等待新消息
            rawData.value = null;
            galleryCards.value = [];
        }
    });
    // 监听消息接收事件（新消息生成完成）
    eventOn(tavern_events.MESSAGE_RECEIVED, (id) => {
        if (id === message_id) {
            console.info('[踏月寻仙] 检测到新消息接收，防抖延迟300ms后重新加载');
            debouncedInitData(300);
        }
    });
    // 监听 MVU 变量更新事件 - 修复：只监听当前楼层的更新
    waitGlobalInitialized('Mvu').then(() => {
        console.info('[踏月寻仙] 开始监听 MVU 变量更新事件（仅当前楼层）');
        // 监听 MVU 变量更新结束事件
        // 🔧 修复：VARIABLE_UPDATE_ENDED 实际接收 3 个参数！
        // 第1个参数: new_variables (新变量)
        // 第2个参数: old_variables (旧变量)
        // 第3个参数: variable_option (变量位置信息，包含 message_id)
        eventOn(Mvu.events.VARIABLE_UPDATE_ENDED, (new_variables, _old_variables, variable_option) => {
            try {
                // 🔧 关键修复：只处理当前楼层的更新事件
                if (variable_option?.message_id !== message_id) {
                    console.debug('[踏月寻仙] 忽略其他楼层的更新事件', {
                        event_message_id: variable_option?.message_id,
                        current_message_id: message_id
                    });
                    return;
                }
                const statData = _.get(new_variables, 'stat_data', null);
                console.info('[踏月寻仙] MVU 变量更新事件触发（当前楼层）', {
                    message_id,
                    has_stat_data: statData !== null && Object.keys(statData).length > 0,
                });
                // 更新当前楼层的数据
                if (statData && Object.keys(statData).length > 0) {
                    try {
                        // 使用 Schema.parse 确保数据结构正确，包括过滤掉数量为0的物品
                        const parsedData = Schema.parse(statData);
                        // 🔧 修复：强制深拷贝，确保 Vue 能检测到所有嵌套对象（如神通列表、背包）的变化
                        // 因为 zod transform 中的 _.pickBy 可能会复用对象引用
                        rawData.value = _.cloneDeep(parsedData);
                        console.info('[踏月寻仙] ✅ MVU 变量已更新', {
                            等级: parsedData.本尊.等级,
                            灵根: parsedData.本尊.灵根,
                            体质: parsedData.本尊.体质,
                            功法: parsedData.本尊.功法,
                            本命兵器: parsedData.本尊.本命兵器,
                            神通数量: Object.keys(parsedData.本尊.神通列表).length,
                            背包物品数: Object.keys(parsedData.本尊.背包).length,
                            法宝数量: Object.keys(parsedData.本尊.法宝).length,
                            灵石: parsedData.本尊.灵石
                        });
                        // 🔧 新增：实时更新图鉴卡片
                        // 由于消息内容可能包含新的 visual_cards，需要重新解析
                        parseCurrentMessageCards();
                    }
                    catch (parseError) {
                        console.error('[踏月寻仙] Schema 解析失败', parseError);
                        // 解析失败时也尝试更新，但记录警告
                        rawData.value = statData;
                    }
                }
                else {
                    console.warn('[踏月寻仙] stat_data 为空，跳过更新');
                }
            }
            catch (e) {
                console.error('[踏月寻仙] 处理 MVU 变量更新失败', e);
            }
        });
    }).catch(e => {
        console.warn('[踏月寻仙] MVU 框架未加载，将仅显示静态数据', e);
    });
    // 计算属性：安全获取各部分数据
    const 世界时钟 = computed(() => rawData.value?.世界时钟 ?? { 纪元: '末法时代', 年份: 1 });
    const 世界地图 = computed(() => rawData.value?.世界地图 ?? {});
    const 世界图志 = computed(() => rawData.value?.世界图志 ?? {});
    const 本尊 = computed(() => rawData.value?.本尊 ?? Schema.parse({}).本尊);
    const 红颜 = computed(() => rawData.value?.红颜 ?? {});
    const NPC图鉴 = computed(() => rawData.value?.NPC图鉴 ?? {});
    const 任务列表 = computed(() => rawData.value?.任务列表 ?? {});
    const 声望系统 = computed(() => rawData.value?.声望系统 ?? {});
    // 是否有数据
    const hasData = computed(() => rawData.value !== null);
    // 是否有图鉴卡片
    const hasGalleryCards = computed(() => galleryCards.value.length > 0);
    return {
        rawData,
        世界时钟,
        世界地图,
        世界图志,
        本尊,
        红颜,
        NPC图鉴,
        任务列表,
        声望系统,
        hasData,
        refresh: initData,
        // 图鉴相关
        galleryCards,
        hasGalleryCards,
        toggleCardFlip,
        parseCurrentMessageCards,
    };
}));
// ============================================================================
// 主题管理 Store (Theme Management Store)
// ============================================================================
export const useThemeStore = defineStore('theme', () => {
    // 从消息楼层变量中读取用户选择的主题ID
    const currentThemeId = ref('default');
    // 当前主题对象
    const currentTheme = computed(() => {
        return getThemeById(currentThemeId.value) || getDefaultTheme();
    });
    // 所有可用主题列表
    const availableThemes = computed(() => themes);
    // 初始化主题 - 从消息楼层变量读取
    const initTheme = () => {
        try {
            const message_id = getCurrentMessageId();
            const variables = getVariables({ type: 'message', message_id });
            const savedThemeId = _.get(variables, 'ui_theme_id', 'red');
            if (typeof savedThemeId === 'string' && getThemeById(savedThemeId)) {
                currentThemeId.value = savedThemeId;
                console.info('[主题] 加载已保存的主题:', savedThemeId);
            }
            else {
                currentThemeId.value = 'red';
                console.info('[主题] 使用默认主题');
            }
        }
        catch (e) {
            console.warn('[主题] 加载主题失败，使用默认主题', e);
            currentThemeId.value = 'red';
        }
    };
    // 切换主题
    const setTheme = (themeId) => {
        if (getThemeById(themeId)) {
            currentThemeId.value = themeId;
            // 保存到消息楼层变量
            try {
                const message_id = getCurrentMessageId();
                updateVariablesWith(vars => {
                    _.set(vars, 'ui_theme_id', themeId);
                    return vars;
                }, { type: 'message', message_id });
                console.info('[主题] 主题已切换并保存:', themeId);
                toastr.success(`已切换至「${currentTheme.value.name}」主题`, '踏月寻仙');
            }
            catch (e) {
                console.error('[主题] 保存主题失败', e);
                toastr.warning('主题切换成功，但保存失败', '踏月寻仙');
            }
        }
        else {
            console.warn('[主题] 无效的主题ID:', themeId);
        }
    };
    // 初始化
    initTheme();
    return {
        currentThemeId,
        currentTheme,
        availableThemes,
        setTheme,
    };
});
